.ChartRow {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-bottom: 5px; 
    min-height: 50px;
}
    
.ChartRow:first-child {
    margin-top: 10px;
}

.ChartRow:last-child {
    margin-bottom: 10px;
}

.ChartRow-left {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex; 
    -webkit-box-pack: end; 
        -ms-flex-pack: end; 
            justify-content: flex-end;
}
  
.u-dottedLine {
    height: 50%; 
    border-top: 1px dotted darkslategray; 
    min-width: 20px;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    margin-left: 4px; 
    margin-right: 4px; 
}

.ChartRow-bar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex; 
    -webkit-box-align: center; 
        -ms-flex-align: center; 
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    border: .5px solid darkslategray;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 40px;
    padding: 2px;
    color: white;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s; 
    -o-text-overflow: clip; 
       text-overflow: clip;
}
  
.LeftBar {
    border-right: 0; 
}
  
.RightBar {
    border-left: 0; 
}
  
.ChartRow-bar:hover {
    -webkit-box-shadow: 0 3px 10px rgba(0,0,0,.2);
            box-shadow: 0 3px 10px rgba(0,0,0,.2);
    -webkit-transform: translate3d(0,-2px,0);
            transform: translate3d(0,-2px,0);
    cursor: pointer;
}
  

@media screen and (max-width: 850px) { 
    .u-dottedLine {
        min-width: 5px;
    }
}